import axios from 'axios'
import { MessageBox } from 'element-ui';
import qs from 'qs'

import store from '@/store'

//axios
axios.defaults.timeout = 10000;
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';
axios.defaults.baseURL = '/djcrm';   //

//POST()
axios.interceptors.request.use((config) => {
	//
    if(config.url != '/djcrm/inneruser/handleoncetoken.do') {
        // if(localStorage.getItem('admin') == null) {
        //     // localStorage.clear();
        //     // location.href = store.state.base.loginIp
        //     return
        // }
        // if(store.getters.doneAdmin.uname != JSON.parse(localStorage.getItem('admin')).uname) {
        //     MessageBox.alert(',', '', {
        //         confirmButtonText: '',
        //         type: 'error',
        //         callback: action => {
        //             // localStorage.clear();
        //             // location.href = store.state.base.loginIp
        //         }
        //     })
        // }
    }
    return config;
},(error) =>{
    return Promise.reject(error);
});

//()
axios.interceptors.response.use((res) =>{
	//
    if(res.data.code == -10001) {
        MessageBox.alert('', '', {
            confirmButtonText: '',
            type: 'error',
            callback: action => {
                location.href = localStorage.getItem('ip');
                localStorage.clear();
                // location.href = store.state.base.loginIp
            }
        })
    }
    if(res.data.code == -10002) {
        MessageBox.alert(',', '', {
            confirmButtonText: '',
            type: 'warning',
            callback: action => {

            }
        })
    }
    return res
}, (error) => {
    return error;
});

export default axios



